	import { mapGetters, mapMutations } from 'vuex';
	
export default {
	data() {
		return {
			tablewareList: ['1', '2', '3', '4', '5', '6'],
			selectItem: ''
		};
	},
	props: {
		isShow: Boolean
	},
	computed: {
		...mapGetters(['remarkInfo'])
	},
	methods: {
		...mapMutations({
			setRemarkInfo: 'SET_REMARK_INFO'
		}),
		clickItem(item) {
			this.selectItem = item;
			this.setRemarkInfo({
				 tableware: item + "份餐具",
				 remark: this.remarkInfo.remark
			})
			this.$emit('close');
		}
	},
	moveHandle() {}
};
